function getDate(type) {
	const date = new Date();
	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year + 2;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}
export default {
	data() {
		return {
			index: 0,
			array: [{name:'保密'},{name:'男'},{name: '女'} ],
			logoImage: "",
			token:'',
			userList:{},
			date: getDate({
				format: true
			}),
			startDate:getDate('start'),
			endDate:getDate('end'),
		}
	},
	created:function(){ 
		var _this = this;
		uni.getStorage({
			 key: 'token',
			 success: function (res) {
				 _this.token = res.data
			}
		})
	},
	mounted:function() {
		let _this = this;
		_this.userInfo();
	},
	methods: {
		bindPickerChange: function(e) {
			var _this = this;
			this.index = e.target.value;
			_this.editUser1()
		},
		logo() {
			var _this = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					_this.logoImage = res.tempFilePaths[0];
					_this.editUser();
				}
			});
		},
		nickname(url,name){
			if(name!=''){
				url = url+'?name='+name
			}
			uni.navigateTo({
				url:url
			})
		},
		bindDateChange: function(e) {
			var _this = this;
			_this.date = e.target.value
			_this.editUser2();
		},
		userInfo:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
			};
			_this.$axios(_this.$baseUrl.userInfo,data).then(res =>{
				if(res.data.status==1){
					_this.userList = res.data.result.user;
					_this.logoImage = res.data.result.user.head_pic;
					_this.index = res.data.result.user.sex;
					_this.date = res.data.result.user.birthday;
				}
			}).catch(error =>{
				
			})
		},
		//修改头像
		editUser:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
				img:_this.logoImage
			};
			_this.$axios(_this.$baseUrl.editUser,data).then(res =>{
				if(res.data.status==1){
					uni.showToast({
						title: res.data.msg,
						icon:'success',
						position:'center',
						duration: 2000
					})
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'fail',
						position:'center',
						duration: 2000
					})
				}
			}).catch(error =>{
				
			})
		},
		//修改男女
		editUser1:function(){
			let _this=this;
