					uni.showToast({
						title: res.data.msg,
						icon:'fail',
						position:'center',
						duration: 2000
					})
				}
			}).catch(error =>{
				
			})
		},
		//修改生日
		editUser2:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
				birth:_this.date,
			};
			_this.$axios(_this.$baseUrl.editUser,data).then(res =>{
				if(res.data.status==1){
